.form-page {
  margin-bottom: 55px;
}

.form-page__header {
  background-color: @light-grey;
}

.form-page__title {
  .title-font;
  margin: 0;
  font-size: 27px;
  padding: 74px 36px;
  margin-bottom: 24px;
}

.order-form {
  margin-left: 30px;
  margin-right: 30px;
}

.order-form__description {
  .general-font;
  font-size: 13px;
  margin-bottom: 22px;
}

.order-form__fieldset {
  border: none;
  margin: 0;
  padding: 0;
  border-top: 1px solid @seafoam-blue;
  margin-bottom: 17px;
}

.order-form__legend {
  .bold;
  line-height: 18px;
  color: @seafoam-blue;
  text-transform: uppercase;
  margin-bottom: 32px;
  padding-right: 24px;
}

.order-form__label {
  .bold;
  line-height: 18px;
  color: black;
  display: block;
}

.order-form__radio {
  position: absolute;
  z-index: -1;
  opacity: 0;
  margin: 10px 0 0 7px;
}

.order-form__radio + label {
  position: relative;
  padding: 0 0 0 63px;
  cursor: pointer;
  margin-bottom: 25px;
}

.order-form__radio + label::before {
  content: " ";
  position: absolute;
  top: -9px;
  left: 0;
  width: 36px;
  height: 36px;
  border: 2px solid @greyish-brown;
  border-radius: 50%;
  background: #ffffff;
}

.order-form__radio:checked + label::after {
  content: " ";
  position: absolute;
  top: 3px;
  left: 12px;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: @seafoam-blue;
}

.order-form__radio:hover + label::before {
  border: 2px solid @seafoam-blue;
}

.order-form__radio:active + label::before {
  border: 2px solid @greyish-brown;
  opacity: 0.3;
}

.order-form__checkbox {
  position: absolute;
  z-index: -1;
  opacity: 0;
  margin: 10px 0 0 20px;
}

.order-form__checkbox + label {
  position: relative;
  padding: 0 0 0 60px;
  cursor: pointer;
  margin-bottom: 43px;
}

.order-form__checkbox + label::before {
  content: " ";
  position: absolute;
  top: -9px;
  left: 0;
  width: 36px;
  height: 36px;
  border: 2px solid @greyish-brown;
  background: #ffffff;
}

.order-form__checkbox:checked + label::after {
  content: "";
  position: absolute;
  top: 3px;
  left: 6px;
  width: 40px;
  height: 30px;
  background-image: url("../img/icon-tick.svg");
  background-repeat: no-repeat;
  background-size: 65%;
}

.order-form__checkbox:hover + label::before {
  border: 2px solid @seafoam-blue;
}

.order-form__checkbox:active + label::before {
  border: 2px solid @greyish-brown;
  opacity: 0.3;
}

.order-form__text {
  .general-font;
  border: none;
  box-shadow: inset 0 -2px 0 0 #000000;
  padding-top: 18px;
  padding-bottom: 18px;
  margin-bottom: 24px;
  font-size: 17px;
  line-height: 18px;
  color: @greyish-brown;
  min-width: 260px;
  padding-left: 17px;

  &:hover {
    box-shadow: inset 0 -2px 0 0 @seafoam-blue;
  }

  &:focus {
    outline: none;
    box-shadow: inset 0 -2px 0 0 @seafoam-blue;
  }
}

::-webkit-input-placeholder {
  opacity: 0.3;
}

::-moz-placeholder {
  opacity: 0.3;
}


/* Firefox 19+ */

:-moz-placeholder {
  opacity: 0.3;
}


/* Firefox 18- */

:-ms-input-placeholder {
  opacity: 0.3;
}

.order-form__textarea {
  .general-font;
  border: 2px solid @greyish-brown;
  min-width: 237px;
  min-height: 120px;
  margin-bottom: 18px;
  font-size: 17px;
  line-height: 18px;
  color: @greyish-brown;
  padding-left: 17px;
  padding-top: 18px;
  resize: none;
  overflow: auto;

  &:hover {
    border: 2px solid @seafoam-blue;
    outline: none;
  }

  &:focus {
    border: 2px solid @seafoam-blue;
    opacity: 1.3;
    outline: none;
  }
}

.order-form__explain {
  .general-font;
  font-size: 14px;
  line-height: 16px;
}

.order-form__button {
  .bold;
  display: block;
  font-size: 18px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  padding: 15px 40px;
  background-color: white;
  border: solid 2px @greyish-brown;
}

@media (min-width: @tablet-width) {
  .form-page__header {
    text-align: center;
  }

  .form-page__title {
    padding: 0;
    margin: 0;
    padding-bottom: 80px;
    padding-top: 21px;
  }

  .order-form__description {
    font-size: 17px;
    line-height: 30px;
    padding-left: 21px;
    padding-top: 58px;
    margin-bottom: 73px;
  }

  .order-form__fieldset {
    border: none;
    padding-left: 24px;
  }

  .order-form__fieldset--special {
    background-color: @light-grey;
  }

  .order-form__label {
    font-size: 19px;
    line-height: 18px;
  }

  .order-form__radio + label {
    padding: 6px 0 0 83px;
    margin-bottom: 53px;
    margin-left: 162px;
  }

  .order-form__radio + label::before {
    width: 48px;
    height: 48px;
    margin-bottom: 54px;
  }

  .order-form__radio:checked + label::after {
    width: 22px;
    height: 22px;
    top: 6px;
    left: 15px;
  }

  .order-form__wrapper-color {
    display: flex;
    flex-wrap: wrap;
    margin-left: 163px;
  }

  .order-form__checkbox + label {
    padding: 6px 0 0 83px;
    margin-bottom: 53px;
    flex-basis: 180px;
  }

  .order-form__checkbox + label::before {
    width: 50px;
    height: 48px;
    background-color: @light-grey;
  }

  .order-form__checkbox:checked + label::after {
    background-size: 88%;
    top: 5px;
    left: 11px;
  }

  .order-form__text {
    font-size: 19px;
    line-height: 18px;
  }
}
