body {
  margin: 0 auto;
  padding: 0;
}
@font-face {
  font-family: "Opensans";
  src: url("../fonts/opensans.woff");
  src: url("../fonts/opensans.woff2");
}
@font-face {
  font-family: "Opensans-bold";
  src: url("../fonts/opensansbold.woff");
  src: url("../fonts/opensansbold.woff2");
}
main {
  width: 320px;
  margin: 0 auto;
  padding: 0;
}
@media (min-width: 768px) {
  main {
    width: 768px;
  }
}
@media (min-width: 1150px) {
  main {
    width: 1150px;
  }
}
.general-font {
  font-family: "Opensans", sans-serif;
  font-size: 15px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 24px;
  letter-spacing: normal;
  color: #444444;
}
.title-font {
  font-family: "Opensans-bold", sans-serif;
  font-size: 25px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: 30px;
  letter-spacing: normal;
  color: #000000;
}
@media (min-width: 768px) {
  .title-font {
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .title-font {
    font-size: 53px;
    line-height: 57px;
  }
}
.bold {
  font-family: "Opensans-bold", sans-serif;
  font-size: 17px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: 20px;
  letter-spacing: normal;
  color: #444444;
}
.list-style {
  list-style: none;
  text-decoration: none;
}
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}
.page-header {
  background-color: white;
  min-height: 79px;
  max-width: 320px;
  margin: 0 auto;
}
.page-header__logo {
  display: block;
  margin-bottom: 18px;
}
.page-header__logo-image {
  padding-left: 116px;
  padding-top: 20px;
}
.main-nav__mobile-wrapper {
  display: flex;
  justify-content: space-between;
  margin-top: 22px;
  position: relative;
  width: 320px;
  margin: 0 auto;
}
.main-nav__toggle {
  position: absolute;
  top: 20px;
  right: 25px;
  display: block;
  width: 21px;
  height: 20px;
  z-index: 2;
  border: none;
  cursor: pointer;
  background-color: transparent;
  padding-right: 27px;
  padding-bottom: 34px;
}
.main-nav__toggle:hover {
  opacity: 0.6;
}
.main-nav__toggle:active {
  opacity: 0.3;
}
.main-nav__mobile-wrapper--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 12px;
  right: 6px;
  width: 20px;
  height: 14px;
  background-image: url("../img/icon-menu-open.svg");
  background-repeat: no-repeat;
}
.main-nav__mobile-wrapper--opened .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 10px;
  right: 5px;
  width: 21px;
  height: 20px;
  background-image: url("../img/icon-menu-close.svg");
  background-repeat: no-repeat;
}
.main-nav__list {
  margin: 0;
  padding: 0;
}
.site-list__item {
  list-style: none;
  border: 1px solid #ececec;
}
.site-list__item__link {
  font-family: "Opensans-bold", sans-serif;
  font-size: 17px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: 20px;
  letter-spacing: normal;
  color: #444444;
  text-decoration: none;
  line-height: 24px;
  display: block;
  padding-bottom: 28px;
  text-align: center;
  padding-top: 25px;
}
.additional-list__item__link {
  text-decoration: none;
  padding: 24px 50px;
}
.additional-list__item {
  display: none;
}
.user-list__item {
  list-style: none;
  border: 1px solid #ececec;
}
.user-list__item__link {
  font-family: "Opensans-bold", sans-serif;
  font-size: 17px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: 20px;
  letter-spacing: normal;
  color: #444444;
  text-decoration: none;
  line-height: 24px;
  display: block;
  text-align: center;
  padding-bottom: 29px;
  padding-top: 25px;
}
@media (max-width: 767px) {
  .main-nav__mobile-wrapper--closed + .site-list {
    display: none;
  }
  .main-nav__mobile-wrapper--closed + .site-list + .user-list {
    display: none;
  }
  .main-nav__mobile-wrapper--opened + .main-nav__list {
    display: flex;
    flex-direction: column;
  }
}
@media (min-width: 768px) {
  .page-header {
    max-width: 768px;
  }
  .main-nav {
    min-height: 88px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
  }
